.skills-diagram {
    position: relative;
    width: 400px;
    height: 403px;
    left: -482px;
    top: -455px;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    max-width: 35em;
    margin: 2em auto 2em;
}

.skills-diagram>dd {
    /* Hide inner attributes */
    display: none;
}

.skills-diagram dt {
    display: inline-block;
    vertical-align: top;
    border-style: solid;
    border-width: 1px;
    border-radius: 50%;
    -webkit-transform: scale(1.2);
    -moz-transform: scale(1.2);
    -o-transform: scale(1.2);
    -ms-transform: scale(1.2);
    transform: scale(1.2);
    /* set defaults to skill-2 */
    width: 2em;
    height: 2em;
    line-height: 2em;
}

.skills-diagram dt:hover {
    -webkit-transform: scale(1.3);
    -moz-transform: scale(1.3);
    -o-transform: scale(1.3);
    -ms-transform: scale(1.3);
    transform: scale(1.3);
}


/* Support vertical mode */

.skills-diagram-vertical dt {
    display: block;
    /* .25em is an equivalent to inline-block's horizontal whitespace margin */
    margin: .25em auto;
}


/* Provide 10 different sizes */

.skills-diagram .skill-2 {
    width: 2em;
    height: 2em;
    line-height: 2em;
    background-color: rgba(201, 155, 129, .3);
    border-color: rgba(201, 155, 129, 1);
    color: rgba(201, 155, 129, 1);
}

